import {SendSmsCode,UserReg} from 'api/user'
export default{
	name:'UserRegister',
	data(){
		return {
			/*密码*/
			password:'',
			/*再次输入*/
			rePassword:'',
			/*状态*/
			status:false,
			mobile:''
		}
	},
	watch:{
		'rePassword'(value){
			if(value == this.password){
				this.status = true
			}
		}
	},
	methods:{
		
		/*完成*/
		next(){
			if(this.status){
				UserReg({
					passWord:this.password,
					repassword:this.rePassword,
					mobile:this.mobile,
					username:this.mobile,
				}).then(res=>{
					if(res.result){
						uni.showToast({
							icon:'success',
							title: '注册成功'
						})
						setTimeout(()=>{
							uni.navigateBack()
						},2000)
						
					}else{
						uni.showToast({
							icon:'none',
							title: res.msg
						})
					}
				})	
			}else{
				uni.showToast({
					icon:'none',
					title: '输入相同密码'
				})
			}
		}
	},
	onLoad (query) { 
	    this.mobile = query.mobile
	}
}
